@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-size: 10px;
}

@media (min-width: 500px) {
  body {
    background: linear-gradient(
      to left,
      #8306b1 -89%,
      rgba(255, 255, 255, 0) 95%
    );
  }
}

.scene__text__underline{border:2px solid #8306b1;border-radius:4px}.scene__text{color:#8306b1;font-size:1.2rem;font-weight:600}@media (min-width: 500px){.scene__text{font-size:1.8rem}}@media (min-width: 1000px){.scene__text{font-size:2.6rem}}.tunnel{width:100%;height:1600px;background-color:black}@media (min-width: 1000px){.tunnel{height:2200px}}.tunnel__text{color:white}.tunnel__sticky{background-color:black;height:100vh;width:100%;position:-webkit-sticky;position:sticky;top:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;overflow:hidden}.tunnel__sticky-inner{width:calc(100% - 40px);height:calc(100% - 40px);position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__lt{position:absolute;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__lt svg *{fill:#8306b1}.tunnel__box__lt svg{filter:drop-shadow(1px 0px 12px #8306b1);-webkit-filter:drop-shadow(1px 0px 12px #8306b1);-moz-filter:drop-shadow(1px 0px 12px #8306b1)}.tunnel__box__lt .spotlight__inner__light__bg:after{width:100%;height:100%;content:"";display:block;position:absolute;top:0;left:0;height:100%}.tunnel__box__lt .spotlight__inner__light{-webkit-clip-path:polygon(0 130px, 100% 0, 100% 100%, 0 171px);clip-path:polygon(0 130px, 100% 0, 100% 100%, 0 171px);position:absolute;left:48px}.tunnel__box__lt .spotlight__inner{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.spotlight__inner__light{width:300px;height:300px}.spotlight__inner__light__bg{width:100%;height:100%;position:relative}.tunnel__box__rt{position:absolute;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__rt svg *{fill:#8306b1}.tunnel__box__rt svg{filter:drop-shadow(1px 0px 12px #8306b1);-webkit-filter:drop-shadow(1px 0px 12px #8306b1);-moz-filter:drop-shadow(1px 0px 12px #8306b1)}.tunnel__box__rt .spotlight__inner__light{-webkit-clip-path:polygon(0 0, 100% 130px, 100% 171px, 0 100%);clip-path:polygon(0 0, 100% 130px, 100% 171px, 0 100%);position:absolute;left:-300px}.tunnel__box__rt svg{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.tunnel__box__rt .spotlight__inner{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.tunnel__box__rt .spotlight__inner__light__bg:after{width:100%;height:100%;content:"";display:block;position:absolute;top:0;left:0;height:100%}

